import React, { Component } from "react";
import "./Configure.css";

import { ButtonType, ButtonWidget } from "@tableau/widgets";

class Configure extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dialogImg: {
        fileName: "",
        fileBase64:
          "data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==",
        height: 0,
        width: 0
      },
      buttonIcon: {
        fileName: "",
        fileBase64:
          "data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==",
        height: 0,
        width: 0
      },
      buttonUpdated: false,
      imageUpdated: false
    };

    this.onDialogImgChange = this.onDialogImgChange.bind(this);
    this.onButtonIconChange = this.onButtonIconChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onDialogImgChange(image) {
    this.setState({ dialogImg: image, imageUpdated: true });
  }

  onButtonIconChange(image) {
    this.setState({ buttonIcon: image, buttonUpdated: true });
  }

  componentWillMount() {
    document.body.style.margin = "0";
    window.tableau.extensions
      .initializeAsync({ configure: this.configure })
      .then(() => {
        const dialogSetting = window.tableau.extensions.settings.get(
          "dialogImg"
        );
        if (dialogSetting) {
          this.setState({ dialogImg: JSON.parse(dialogSetting) });
        }
        const iconSetting = window.tableau.extensions.settings.get(
          "buttonIcon"
        );
        if (iconSetting) {
          this.setState({ buttonIcon: JSON.parse(iconSetting) });
        }
      });
  }

  onSubmit() {
    if (this.state.imageUpdated) {
      window.tableau.extensions.settings.set(
        "dialogImg",
        JSON.stringify(this.state.dialogImg)
      );
    }
    if (this.state.buttonUpdated) {
      window.tableau.extensions.settings.set(
        "buttonIcon",
        JSON.stringify(this.state.buttonIcon)
      );
    }
    window.tableau.extensions.settings.saveAsync().then(() => {
      window.tableau.extensions.ui.closeDialog("Config Dialog Closed");
    });
  }

  render() {
    return (
      <div className="configFrame">
        <p className="title">Image Popper Configuration</p>
        <ImageSelector
          id="buttonIconSelector"
          onChange={this.onButtonIconChange}
          image={this.state.buttonIcon}
        >
          Button Icon
        </ImageSelector>
        <ImageSelector
          id="dialogImgSelector"
          onChange={this.onDialogImgChange}
          image={this.state.dialogImg}
        >
          Dialog Image
        </ImageSelector>
        <div className="submitButtonContainer container">
          <ButtonWidget
            buttonType={ButtonType.Go}
            handleClick={() => this.onSubmit()}
            testId="submit"
            disabled={!this.state.buttonUpdated && !this.state.imageUpdated}
          >
            Submit
          </ButtonWidget>
        </div>
      </div>
    );
  }
}

class ImageSelector extends Component {
  constructor(props) {
    super(props);

    this.onFileChange = this.onFileChange.bind(this);
  }

  onFileChange(event) {
    const newFile = event.target.files[0];
    let reader = new FileReader();
    reader.onloadend = () => {
      let tempImg = new Image();
      tempImg.src = reader.result;
      this.props.onChange({
        fileName: newFile.name,
        fileBase64: reader.result,
        height: tempImg.height,
        width: tempImg.width
      });
    };
    reader.readAsDataURL(newFile);
  }

  displayName(fileName) {
    if (fileName.length > 20) {
      const first15 = fileName.slice(0, 14);
      const fileExt = fileName.split(".").pop();
      return `${first15}...${fileExt}`;
    } else {
      return fileName;
    }
  }

  //TODO: Is it confusing to display the size when thats not how big it will be in the dashboard?
  //TODO: Consult with Alyshia.
  //TODO: Also consider looking at image object on the web and button configure dialog to explore patterns
  render() {
    const inputid = `${this.props.id}_inputbutton`;
    if (this.props.image.fileName != "") {
      return (
        <div className="imageSelector">
          <p className="sectionTitle">{this.props.children}</p>
          <div className="container imageSelectorInner">
            <ImageSquare
              src={this.props.image.fileBase64}
              height="144px"
              width="144px"
            />
            <div className="detailsColumn">
              <p>
                <b>File Name:</b> {this.displayName(this.props.image.fileName)}
              </p>
              <p>
                <b>Size:</b> {this.props.image.width} x{" "}
                {this.props.image.height}
              </p>
              <input
                type="file"
                id={inputid}
                style={{ display: "none" }}
                onChange={this.onFileChange}
              />
              <ButtonWidget
                buttonType={ButtonType.Outline}
                style={{ marginTop: "24px" }}
                handleClick={() => document.getElementById(inputid).click()}
              >
                Replace
              </ButtonWidget>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className="imageSelector">
          <p className="sectionTitle">{this.props.children}</p>
          <div className="container imageSelectorInner">
            <input
              type="file"
              id={inputid}
              style={{ display: "none" }}
              onChange={this.onFileChange}
            />
            <ButtonWidget
              buttonType={ButtonType.Outline}
              style={{ marginTop: "24px" }}
              handleClick={() => document.getElementById(inputid).click()}
            >
              Upload
            </ButtonWidget>
          </div>
        </div>
      );
    }
  }
}

const ImageSquare = ({ src, width, height }) => {
  const imgStyle = {
    width: width,
    height: height,
    backgroundImage: `url(${src})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "contain",
    backgroundPosition: "left",
    cursor: "pointer"
    // border: '1px solid rgb(230, 230, 230)',
    // borderRadius: '1px',
    //TODO: How can I get this damn border to work? Maybe just a box around the full square but with padding.
  };
  return (
    <div className="imageSquare">
      <div style={imgStyle} />
    </div>
  );
};

export default Configure;



