import React, { Component } from "react";
import "./ImageDialog.css";

class ImageDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      configured: false
    };

    this.onClick = this.onClick.bind(this);
    this.launchConfigure = this.launchConfigure.bind(this);
  }

  componentWillMount() {
    window.tableau.extensions
      .initializeAsync({ configure: this.configure })
      .then(() => {
        const imageSetting = window.tableau.extensions.settings.get("dialogImg");
        if (imageSetting) {
          this.setState({ ...JSON.parse(imageSetting), configured: true });
        }
      });
  }

  componentDidMount() {
    document.body.style.margin = "0";
  }

  onClick() {
    window.tableau.extensions.ui.closeDialog("Image Dialog Closed");
  }

  launchConfigure() {
    window.tableau.extensions.ui.closeDialog("Launch Configure");
  }

  render() {
    console.log(this.state);
    if (this.state.configured) {
      return (
        <img
          alt="Chosen pic"
          src={this.state.fileBase64}
          className="hoverPointer noScrollbar"
          onClick={() => this.onClick()}
        />
      );
    } else {
      return (
        <div className="flexContainer">
          <div className="dialogMessage">
            <span onClick={this.launchConfigure} className="link">
              <i>Configure this Extension</i>
            </span>
            <span> to choose an image to display in this dialog.</span>
          </div>
        </div>
      );
    }
  }
}

export default ImageDialog;



