import React, { Component } from 'react';
import './ImagePopper.css';
import defaultButton from '../../images/default_image.png';

class ImagePopper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      windowHeight: window.innerHeight
    };

    this.configure = this.configure.bind(this);
    this.showImageDialog = this.showImageDialog.bind(this);
  }

  componentWillMount() {
    window.tableau.extensions
      .initializeAsync({ configure: this.configure })
      .then(() => {
        let buttonSetting = window.tableau.extensions.settings.get(
          'buttonIcon'
        );
        // If no existing button icon. We save the existing one to settings before loading it.
        if (!buttonSetting) {
          const imgToSave = {
            fileName: 'default_image.png',
            fileBase64: defaultButton,
            height: 1200,
            width: 1200
          };
          buttonSetting = JSON.stringify(imgToSave);
          window.tableau.extensions.settings.set('buttonIcon', buttonSetting);
          window.tableau.extensions.settings.saveAsync();
        }
        const buttonImage = JSON.parse(buttonSetting).fileBase64;
        this.setState({ buttonImage: buttonImage });
      });
  }

  componentDidMount() {
    window.addEventListener('resize', () =>
      this.setState({ windowHeight: window.innerHeight })
    );
  }

  configure() {
    const popupUrl = `${window.location.origin}${window.location.pathname}#/config`;
    window.tableau.extensions.ui
      .displayDialogAsync(popupUrl, '', { height: 538, width: 402 })
      .then(closePayload => {
        const buttonSetting = window.tableau.extensions.settings.get(
          'buttonIcon'
        );
        const buttonImage = buttonSetting
          ? JSON.parse(buttonSetting).fileBase64
          : defaultButton;
        this.setState({ buttonImage: buttonImage });
      })
      .catch(error => {
        switch (error.errorCode) {
          case window.tableau.ErrorCodes.DialogClosedByUser:
            console.log('Config Dialog was closed by user.');
            break;
          default:
            console.error(error.message);
        }
      });
  }

  showImageDialog() {
    const imageSetting = window.tableau.extensions.settings.get('dialogImg');
    const dims = {
      width: imageSetting ? JSON.parse(imageSetting).width : 408,
      height: imageSetting ? JSON.parse(imageSetting).height : 200
    };
    const popupUrl = `${window.location.origin}${window.location.pathname}#/dialog`;
    window.tableau.extensions.ui
      .displayDialogAsync(popupUrl, '', dims)
      .then(closePayload => {
        console.log(closePayload);
        if (closePayload === 'Launch Configure') {
          this.configure();
        }
      })
      .catch(error => {
        switch (error.errorCode) {
          case window.tableau.ErrorCodes.DialogClosedByUser:
            console.log('Image Dialog was closed by user.');
            break;
          default:
            console.error(error.message);
        }
      });
  }

  render() {
    let img = new Image();
    img.src = this.state.buttonImage;
    const imgStyle = {
      width: '100%',
      height: this.state.windowHeight - 20,
      backgroundImage: `url(${img.src})`,
      backgroundRepeat: 'no-repeat',
      backgroundSize: 'contain',
      backgroundPosition: 'center',
      cursor: 'pointer'
    };
    return <div style={imgStyle} onClick={() => this.showImageDialog()} />;
  }
}

export default ImagePopper;



